@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,400;0,500;0,600;1,100;1,300;1,400;1,500&display=swap);

.input-item{
	margin:0.2vmax;
	width: 20vmax;
	height: 2vmax;
	border-radius: 25px;
	box-shadow:inset 5px 5px 8px #cbced1, inset -5px -5px 8px #ffffff;
	border: none;
	background-color: #ececec;
}
.buttons{	
	display:grid;
	grid-gap: 4vmax;
	grid-template-areas:
	"submmit clear";
}
.submmit{
	background-color: #ececec;
	cursor:pointer;
	grid-area:submmit;
	cursor:pointer;
	height: 3vw;
	width: 3vw;
	border-radius: 50%;
	box-shadow: -10px -10px 15px rgba(255,255,255,0.5),
	10px 10px 15px rgba(70,70,70,0.12);
	border: none;
}
.submmit:active{
	border-radius: 50%;
	box-shadow:inset 5px 5px 8px #cbced1, inset -5px -5px 8px #ffffff;

}

.clear{
	background-color: #ececec;
	grid-area:clear;
	cursor:pointer;
	height: 3vw;
	width: 3vw;
	border-radius: 50%;
	box-shadow: -10px -10px 15px rgba(255,255,255,0.5),
	10px 10px 15px rgba(70,70,70,0.12);
	border: none;
}	
.clear:active{
	border-radius: 50%;
	box-shadow:inset 5px 5px 8px #cbced1, inset -5px -5px 8px #ffffff;

}

.input-file{
	width:90%;
	display: none ;
}
.input-container{
	display:grid;
	grid-template-rows: 60% auto;	
	grid-gap: 1.5vmax;
	align-items:center;
	justify-items: left;
	padding:1vw;
	border-radius: 15px;

}
.input-upper{
	align-self: center;
	justify-self: center;

}
.input-lower{
	align-self: center;
	justify-self: center;
}
.input-changer{
	align-self: center;
	justify-self: center;
}

.input-buttons{
	display:grid;
	grid-template-areas:
	"png svg ngc";
	grid-gap: 6vmax;
	

}
.input-boxes{
	display:grid;
	grid-template-rows: 70% auto;
	grid-gap: 1vmax;
	align-items:center;
	justify-items: left;
	padding:1vw;
}

.input-item::-webkit-input-placeholder{
	font-weight: 600;
	letter-spacing: 1px;
	text-align: center;

}

.input-item:-ms-input-placeholder{
	font-weight: 600;
	letter-spacing: 1px;
	text-align: center;

}

.input-item::placeholder{
	font-weight: 600;
	letter-spacing: 1px;
	text-align: center;

}
.input-item[type='text']{
	font-weight: 600;
	letter-spacing: 1px;
	color: #282828;
	text-align: center;
}

.png-container{
	grid-area:png;

}
.svg-container{
	grid-area:svg;
}
.ngc-container{
	grid-area:ngc;
}
.submit-button{
 background-color:#ececec;
 color:black;
 box-shadow: -10px -10px 15px rgba(255,255,255,0.5),
 10px 10px 15px rgba(70,70,70,0.12);;
 display: inline-block;
 border-radius: 0.5vmax;
 cursor: pointer;
 padding: 1vmax;
 text-align: center;
 border: 1px solid #ececec;
}
.submit-button:active{
 box-shadow:inset 5px 5px 8px #cbced1, inset -5px -5px 8px #ffffff;

}


.item-wrapper{
	display:grid;
	grid-template-columns: 3fr repeat(6,1fr);
	grid-template-areas:
	"item png svg ngc info edit check";

	grid-gap:1vw ;
	padding:0 1vmax;
	
	align-items:center;
	border:0.15vmax solid rgb(30, 127, 165);
	border-radius: 0.5vmax;
	overflow:hidden;
	height:100%;
}
.list-item{
	grid-area: item;
	overflow: auto;
}
.btn{
	cursor:pointer;
	text-align: center;
	border:0.15vmax solid rgb(53, 153, 219);
	border-radius:0.5vmax;
	background-color:white;
}
.btn:hover{
	background-color: rgb(199, 188, 188);
}
.png{
	grid-area: png;
	text-align: center;
}
.svg{
	grid-area: svg;
	text-align: center;
}
.ngc{
	grid-area: ngc;
	text-align: center;
}
.info{
	grid-area: info;
}
.edit{
	grid-area: edit;

}
.check{
	grid-area: check;

}
.list-container{
	display:grid;
	width:70vw;
	margin-top:1vmax;
	grid-template-columns: 1fr;
	grid-template-rows: auto minmax(50vmax,auto);
	grid-gap:1.5vmax;
	justify-content:center;
}

.items-container{
	display:grid;
	grid-auto-flow: row;
	grid-auto-rows:3vmax;
	grid-gap:1.5vmax;
	align-items:center;

	padding: 1vw;
	min-height: 50vmax;

}
.items-not-checked{
	border:3px solid rgb(39, 240, 139);
	border-radius: 1.5vmax;
	background-color:rgb(201, 243, 218);
}
.items-checked{
	border:3px solid rgb(240, 180, 39);
	border-radius: 1.5vmax;
	background-color:rgb(240, 234, 181);
}
.switch-checked{
	justify-self:end;
	width:11vw;
	cursor:pointer;
	text-align: center;
	border:0.15vmax solid rgb(53, 153, 219);
	border-radius:0.5vmax;
	background-color:white;
}

.sidebar{
	position:fixed;
	display:grid;
	grid-template-rows: 15vw 4fr 1fr;
	grid-template-columns:9fr 1fr;
	grid-template-areas: 
	"img close"
	"info info"
	"files files";
	grid-row-gap:1vmax;
	margin-top:1vmax;
	left:72.5vw;
	top:0;
	width:23vw;
	height: 30vmax;
	padding:1vw;
	border: 0.2vw solid rgba(18, 95, 218, 0.424);
	background-color:rgba(114, 205, 233, 0.363);
	border-radius: 1vmax;
}
.info-container{
	display:grid;
	grid-area:info;
	grid-template-rows:1fr 1fr 1fr;
	grid-template-columns:1fr 4fr;
	grid-template-areas:
	"desc descText"
	"client clientText"
	"address addressText";
	grid-gap:1vmax;
	align-items: center;
}
.files-container{
	display:grid;
	grid-area:files;
	grid-template-columns:1fr 1fr 1fr;
	grid-template-areas:
	"png svg ngc";
	align-items: center;
}
.png-container{
	grid-area:png;
	text-align: center;
}
.svg-container{
	grid-area:svg;
	text-align: center;
}
.ngc-container{
	grid-area:ngc;
	text-align: center;
}

.description{
	grid-area:desc;
}
.description-text{
	grid-area:descText;
}
.client{
	grid-area:client;
}
.client-text{
	grid-area:clientText;
}
.address{
	grid-area:address;
}
.description-text{
	grid-area:addressText;
}
.close-icon{
	grid-area:close;
	justify-self:right;
	cursor:pointer;
	-webkit-transform:scale(1.5);
	        transform:scale(1.5);
}

.text{
	padding: 0.5vmax;
	height:1.1vmax;
	background-color:white;
	overflow-x:auto;
	overflow-y:hidden;
}
.text-wrapper{
	border: 0.2vw solid rgba(18, 95, 218, 0.424);
	border-radius:1vmax;
	overflow:hidden;
}
.image{
	justify-self:center;
	align-self:center;
	max-width:100%;
	max-height:100%;
	border-radius:1vmax;
}

.main-container{
	display:grid;
	margin-top:1vmax;
	justify-items: center;
}

.auth-container{
	display:grid;
	grid-template-columns:1fr 1fr;
	grid-template-areas:
	"login register";
	grid-gap:1vmax;
	width:70vw;
	min-height:30vmax;
}

.login{
	grid-area:login;
}
.register{
	grid-area:register;
}
.container{
	display:grid;
	grid-template-rows: repeat(2,1fr);
	grid-template-areas:
	"title"
	"form";
	margin:1vmax;
	justify-items: center;
	align-items: center;
	box-shadow: -10px -10px 15px rgba(255,255,255,0.5),
	10px 10px 15px rgba(70,70,70,0.12);;
	border-radius: 1.5vmax;
	height: 40vmax;
}
.title-container{
	min-width:15vw;
	text-align:center;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	padding:0.4vmax;
	grid-area:title;
	border-radius:0.5vmax;
	color: black;
	
}

.form-container{
	height:100%;
	width:100%;
	grid-area:form;
	display:grid;
	grid-template-rows:1fr 1fr 1fr;
	grid-template-areas:
	"username"
	"password"
	"submit";
	justify-items: center;
	align-items: center;
}
.input{
	width:13vw;
}
.username-input{
	grid-area:username;
	width: 50%;
    border-radius: 15px;
	box-shadow:inset 5px 5px 8px #cbced1, inset -5px -5px 8px #ffffff;
	border: none;
	height:50px;
	background-color: #ececec;

}
.username-input[type='text']{
	font-weight: 600;
	letter-spacing: 1px;

	text-align: center;
	color: #282828;
}

.password-input{
	grid-area:password;
	width: 50%;
	border-radius: 15px;
	box-shadow:inset 5px 5px 8px #cbced1, inset -5px -5px 8px #ffffff;
	border: none;
	height:50px;
	background-color: #ececec;

}
.password-input[type='password']{
	font-weight: 600;
	letter-spacing: 1px;

	text-align: center;
	color: #282828;
}

.password-input::-webkit-input-placeholder{

	font-weight: 600;
	letter-spacing: 1px;
	
	text-align: center;
}

.password-input:-ms-input-placeholder{

	font-weight: 600;
	letter-spacing: 1px;
	
	text-align: center;
}

.password-input::placeholder{

	font-weight: 600;
	letter-spacing: 1px;
	
	text-align: center;
}
.username-input::-webkit-input-placeholder{
	
	font-weight: 600;
	letter-spacing: 1px;

	text-align: center;
}
.username-input:-ms-input-placeholder{
	
	font-weight: 600;
	letter-spacing: 1px;

	text-align: center;
}
.username-input::placeholder{
	
	font-weight: 600;
	letter-spacing: 1px;

	text-align: center;
}
.submit{
	height: 5vh;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	grid-area:submit;
	padding:0.3vmax;
	min-width:6vw;
	cursor:pointer;
	text-align: center;
	box-shadow: -10px -10px 15px rgba(255,255,255,0.5),
	10px 10px 15px rgba(70,70,70,0.12);;
	border-radius:1vmax;
	border: none;
	background-color:#ececec;
}
.submit:active{
	box-shadow:inset 5px 5px 8px #cbced1, inset -5px -5px 8px #ffffff;

}
.title-text{
	font-size: 3vw;
	font-weight: 600;
	letter-spacing: 4px;
	font-family: Montserrat;
	
	

}

